/* Variables */

$max_width: 1180px;

$f_family_default: Arial, Helvetica, sans-serif;
$f_size_default: 16px;
$line_height_default: 1.3;
$color_default: #4d4d4d;
$bg_default: #fff;
$bg_grey: #f6f6f6;
$bg_overlay: #585858;

/* Header */
$color_nav_link: $color_default;
$color_nav_link_mobile: #fff;
$f_size_nav_link: $f_size_default;

/* Reviews */
$bg_slider_dark: #212121;

/* Footer */
$color_white_footer: #fff;
$bg_footer_social_default: #fff;
$color_footer_social_default: $color_default;

/* Mixins */

@mixin flex_row_wrap {
  display: flex;
  flex-flow: row wrap;
}

@mixin flex_space_between {
  @include flex_row_wrap;
  justify-content: space-between;
}

@mixin flex_space_between_reverse {
  display: flex;
  flex-flow: row-reverse wrap;
  justify-content: space-between;
}

/* Button mixin */
$page_btn_border_color: #4d4d4d;
$page_btn_color: $color_default;
@mixin page_btn {
  font-family: "asap_medium", $f_family_default;
  font-size: 16px;
  color: $page_btn_color;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid $page_btn_border_color;
  min-width: 160px;
  padding: 15px 20px;
  cursor: pointer;
}

@mixin placeholder {
  &::-webkit-input-placeholder {
    @content;
  }
  &:-moz-placeholder {
    @content;
  }
  &::-moz-placeholder {
    @content;
  }
  &:-ms-input-placeholder {
    @content;
  }
}
