@import 'fonts.scss';
@import 'variables.scss';

/* Global */
html,
body {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

p {
  margin-bottom: 10px;
}

button {
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  vertical-align: top;
}

body {
  font: #{$f_size_default}/#{$line_height_default} $f_family_default;
  color: $color_default;
  background-color: $bg_default;
  min-width: 320px;

  &.body--overflow {
    overflow: hidden;
  }
}

.container {
  max-width: $max_width;
  padding-right: 5px;
  padding-left: 5px;
  margin: 0 auto;
}

/* Header */
.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  transition: all .3s;
  z-index: 100;

  &__inner {
    @include flex_space_between;
    padding-top: 10px;
    padding-bottom: 10px;
    transition: padding .2s;
  }

  &--sticky {
    background-color: #fff;
    box-shadow: 0 1px 5px rgba(0, 0, 0, .1);

    & .header__inner {
      padding-top: 0px;
    }

    & .logo {
      margin-top: 10px;
    }

    & .logo__img {
      width: 95%;
    }
  }
}

.logo {
  display: inline-block;

  &__link {
    display: block;
  }

  &__img {
    transition: all .2s;
  }
}

.btn-menu {
  width: 50px;
  height: 50px;
  border: none;
  background-color: transparent;
  position: relative;
  padding: 5px;
  cursor: pointer;
  outline: none;
  z-index: 101;

  &__line {
    width: 80%;
    height: 2px;
    background-color: $color_default;
    position: absolute;
    top: 50%;
    right: 5px;
    transform: translateY(-50%);

    &::before,
    &::after {
      content: "";
      background-color: $color_default;
      height: 2px;
      position: absolute;
      right: 0;
    }

    &::before {
      width: 85%;
      top: -10px;
    }

    &::after {
      width: 65%;
      bottom: -10px;
    }
  }

  &--close .btn-menu__line {
    background-color: #fff;

    &::before,
    &::after {
      background-color: #fff;
    }
  }
}

.nav {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: transparentize($color: $bg_overlay, $amount: .05);
  overflow-y: auto;
  transform: translateX(-110%);
  transition: transform .3s;
  z-index: 10;

  &__list {
    margin: auto;
    text-align: center;
  }

  &__link {
    font-family: "asap_regular", $f_family_default;
    font-size: $f_size_nav_link;
    color: $color_nav_link_mobile;
    text-transform: uppercase;
    display: block;
    padding: 8px 0 6px;
    margin: 10px 0;
    position: relative;

    &--active {
      text-transform: capitalize;
    }
  }

  &--opened {
    transform: translateX(0);
  }
}

.page-btn {
  @include page_btn;
}

/* Home section */
.home {
  background: #e1e1e1 url(../img/bg-home.jpg) left / cover no-repeat;
  text-align: center;
  padding-top: 96px;
  margin-bottom: 50px;

  &__img {
    max-width: 575px;
    margin: 0 auto 20px;
  }

  &__desc {
    font-family: "asap_regular", $f_family_default;
    max-width: 570px;
    margin: 0 auto;
  }

  &__title {
    font-family: "asap_medium", $f_family_default;
    font-size: 3em;
    color: $color_default;
    margin-bottom: 10px;
  }

  &__text {
    font-style: inherit;
    font-weight: inherit;
    color: transparentize($color: $color_default, $amount: .54);
    display: block;
    margin-bottom: 16px;
  }

  &__btn {
    padding: 17px 5px 13px;
    margin: 10px;

    &.about__btn {
      font-family: "asap_regular", $f_family_default;
      border-color: #fff;
      background-color: #fff;
      box-shadow: 0px 18px 26.68px 2.32px rgba(0, 0, 0, .1);
    }
  }
}

/* About section */
.section-heading {
  font-family: "asap_bold", $f_family_default;
  font-size: 23px;
  font-weight: bold;
  line-height: 1.07;
  color: transparentize($color: $color_default, $amount: .2);
  text-transform: uppercase;
  display: inline-block;
  padding-top: 10px;
  position: relative;

  &::before {
    content: "";
    width: 60px;
    height: 4px;
    background-color: transparentize($color: $color_default, $amount: .2);
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  &__part {
    display: block;
  }
}

.about {
  text-align: center;
  margin-bottom: 50px;

  &__img {
    background-color: $bg_grey;
    margin-bottom: 20px;
  }

  &__text {
    font-family: "asap_regular", $f_family_default;
    color: inherit;
    line-height: 1.65;
    margin-bottom: 10px;
  }
}

/* Services section */
.services {
  text-align: center;
  background-color: $bg_grey;
  padding-top: 50px;
  padding-bottom: 50px;

  &__list {
    @include flex_row_wrap;
    justify-content: space-around;
    align-items: baseline;
  }

  &__heading {
    margin-bottom: 35px;
  }

  &__item {
    margin-bottom: 30px;
    width: calc(100% / 3);
    min-width: 240px;
    padding: 0 10px;
  }

  .services__ico {
    width: 77px;
    height: 77px;
    display: inline-block;
    margin: 0 auto 10px;
    background: url(../img/sprite-services.png) -10px -105px no-repeat;
  }

  &__item:nth-child(2) .services__ico {
    width: 76px;
    height: 62px;
    background-position: -216px -10px;
  }

  &__item:nth-child(3) .services__ico {
    width: 67px;
    height: 75px;
    background-position: -120px -106px;
  }

  &__item:nth-child(4) .services__ico {
    width: 90px;
    height: 75px;
    background-position: -10px -10px;
  }

  &__item:nth-child(5) .services__ico {
    width: 57px;
    height: 81px;
    background-position: -216px -92px;
  }

  &__item:nth-child(6) .services__ico {
    width: 76px;
    height: 76px;
    background-position: -120px -10px;
  }
}

.service-block {
  &__heading {
    font-family: "asap_medium", $f_family_default;
    font-size: 18px;
    max-width: 210px;
    margin: 0 auto;
  }
}

/* Skills section */
.skills {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;

  &__heading {
    margin-bottom: 20px;
  }

  &__content {
    width: 100%;
    max-width: 995px;
    margin: 0 auto;
  }
}

/* Portfolio section */
.portfolio {
  text-align: center;
  background-color: $bg_grey;
  padding-top: 30px;
  padding-bottom: 30px;

  &__heading {
    margin-bottom: 30px;
  }

  &__item {
    max-width: 400px;
    margin: 0 auto 30px;
  }

  &__link {
    display: block;
    position: relative;

    .hover-effect {
      background-color: #212121;
      display: flex;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 0;
      transition: opacity .2s;

      &__ico {
        width: 56px;
        height: 55px;
        background-color: #fff;
        margin: auto;
        position: relative;

        &::after {
          content: "";
          background: url(../img/ico-arrow.png) no-repeat;
          width: 10px;
          height: 16px;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }

    &:hover .hover-effect {
      opacity: 1;
      box-shadow: 0px 0px 36.75px 12.25px rgba(0, 0, 0, .15);
    }
  }

  &__btn {
    background-color: #fff;
    border-color: #fff;
    box-shadow: 0px 18px 26.68px 2.32px rgba(0, 0, 0, .1);
  }
}

/* Reviews section */
.reviews {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 60px;

  &__heading {
    margin-bottom: 20px;
  }
}

.swiper-container {
  width: 100%;
  max-width: 955px;
}

.reviews-slider {
  padding-bottom: 40px;

  &__photo {
    max-width: 400px;
    margin: 0 auto 10px;
  }

  &__desc {
    text-transform: uppercase;
  }

  &__username {
    font-family: "asap_medium", $f_family_default;
    font-size: 22px;
    color: #212121;
    text-transform: uppercase;
    margin-bottom: 10px;
  }

  &__userpos {
    font-family: "asap_italic", $f_family_default;
    font-size: 12px;
    font-style: italic;
    color: inherit;
    display: block;
  }

  &__text {
    font-size: 1rem;
    line-height: 1.625;
    color: transparentize($color: $bg_slider_dark, $amount: .4);
  }
}

.reviews-slider-container {
  & .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    background: transparent;
    border: 1px solid $bg_slider_dark;
    border-radius: 0;
    opacity: 1;

    &-active {
      background: $bg_slider_dark;
    }
  }
}

.swiper-container-horizontal
  > .swiper-pagination-bullets
  .swiper-pagination-bullet {
  margin: 0 2.5px;
}

/* Progress section */
.progress {
  text-align: center;
  background-color: $bg_grey;
  padding-top: 30px;
  padding-bottom: 10px;
  margin-bottom: 60px;

  &__list {
    @include flex_row_wrap;
    justify-content: space-around;
    align-items: baseline;
  }

  &__item {
    margin-bottom: 30px;
    width: calc(100% / 4);
    min-width: 240px;
  }

  &__ico {
    width: 98px;
    height: 98px;
    background: url(../img/sprite-progress.png) -10px 0 no-repeat;
    display: inline-block;
    margin-bottom: 10px;
  }

  &__item:nth-child(2) {
    .progress__ico {
      width: 77px;
      height: 77px;
      background-position: -118px -21px;
    }
  }

  &__item:nth-child(3) {
    .progress__ico {
      width: 64px;
      height: 74px;
      background-position: -205px -24px;
    }
  }

  &__item:nth-child(4) {
    .progress__ico {
      width: 70px;
      height: 82px;
      background-position: -279px -16px;
    }
  }

  &__desc {
    font-family: "asap_regular", $f_family_default;
    font-size: 17px;
    text-transform: uppercase;
    padding: 0 10px;
  }

  &__counter,
  &__name {
    display: block;
  }

  &__counter {
    font-family: "asap_medium", $f_family_default;
    font-size: 21px;
  }
}

/* Footer */
.footer {
  text-align: center;
  background: $bg_grey url(../img/bg-footer.jpg) left / cover no-repeat;
  padding-top: 30px;

  &-bottom {
    border-top: 1px solid $color_white_footer;
    padding-top: 26px;
    padding-bottom: 30px;
  }
}

/* Contact form */
.form-container {
  width: 100%;
  max-width: 768px;
  margin: 0 auto;

  &__heading {
    color: $color_white_footer;
    margin-bottom: 30px;

    &::before {
      background-color: #c1c1c1;
    }
  }
}

.contact-form {
  width: 100%;
  padding-bottom: 30px;

  &__field {
    width: 100%;
    display: inline-block;
    margin-bottom: 30px;
  }

  &__inp,
  &__msg {
    width: 100%;
    display: inline-block;
    border: 1px solid $color_white_footer;
    background-color: $color_white_footer;
    padding: 20px 9px 17px;

    @include placeholder {
      transition: text-indent .3s;
      font-size: 1rem;
      color: #212121;
    }
  }

  &__msg {
    padding-top: 20px;
    margin-bottom: 25px;
    resize: none;
    height: 206px;
  }

  &__btn {
    min-width: 124px;
    background-color: $color_white_footer;
    border-color: $color_white_footer;
    box-shadow: 0px 18px 26.68px 2.32px rgba(0, 0, 0, .1);

    &_ico {
      width: 15px;
      height: 13px;
      background: url(../img/ico-form-btn.png) no-repeat;
      display: inline-block;
      position: relative;
      top: 1px;
      left: 7px;
    }
  }
}

input::-moz-placeholder,
textarea::-moz-placeholder {
  opacity: 1;
}

.footer-social {
  @include flex_row_wrap;
  justify-content: center;
  margin-bottom: 20px;

  &__item {
    margin: 0 4.3px;
  }

  &__link {
    font-size: 1rem;
    width: 27px;
    height: 27px;
    background-color: $bg_footer_social_default;
    display: flex;
    transition: all .3s;

    &_ico {
      height: 1rem;
      margin: auto;

      & path {
        fill: #4d4d4d;
      }
    }
  }
}

.copyright {
  font-family: "asap_regular", $f_family_default;
  font-size: inherit;
  color: $color_white_footer;
}

.scroll-top {
  width: 45px;
  height: 41px;
  background-color: $color_white_footer;
  position: fixed;
  right: 4%;
  bottom: 30%;
  cursor: pointer;
  transition: opacity .3s;
  display: none;

  &::before {
    content: "";
    width: 14px;
    height: 9px;
    background: url(../img/ico-top.png) no-repeat;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &:hover {
    opacity: .8;
  }
}

/* Tablet */
@media screen and (min-width: 767px) {
  .container-row-reverse {
    @include flex_space_between_reverse;
  }

  /* Home section */
  .home {
    &__inner {
      align-items: center;
    }

    &__img {
      width: 50%;
    }

    &__desc {
      text-align: center;
      width: 50%;
    }

    &__btns {
      margin-left: -10px;
    }
  }

  /* About section */
  .about {
    &__inner {
      align-items: center;
    }

    &__img {
      width: 50%;
      max-width: 570px;
    }

    &__desc {
      width: 50%;
      text-align: left;
      padding-right: 10px;
    }

    &__heading {
      &::before {
        left: 0;
        transform: translateX(0);
      }
    }
  }

  /* Portfolio section */
  .portfolio {
    &__content {
      @include flex_row_wrap;
      justify-content: space-evenly;
    }

    &__item {
      margin: 0 0 30px;
    }
  }

  /* Reviews section */
  .reviews-slider-container {
    padding-top: 70px;
    padding-bottom: 44px;
    box-shadow: 0 0 0 1px $bg_slider_dark;
  }

  .reviews-slider {
    padding-bottom: 49px;

    &__item {
      @include flex_row_wrap;
    }

    &__photo {
      max-width: 160px;
      margin: 0 30px 0 105px;
    }

    &__desc {
      width: 60%;
      text-align: left;
    }
  }

  .swiper-pagination-fraction,
  .swiper-pagination-custom,
  .swiper-container-horizontal > .swiper-pagination-bullets {
    bottom: 33px;
  }

  /* Contact form */
  .contact-form {
    &__row {
      @include flex_space_between;
    }

    &__field {
      width: calc(100% / 2 - 15px);
    }
  }
}

/* Desktop */
@media screen and (min-width: 1023px) {
  /* Header */
  .logo {
    margin-top: 21px;
  }

  .btn-menu {
    display: none;
  }

  .nav {
    position: relative;
    background-color: transparent;
    transform: translateX(0);

    &__list {
      margin: 0;
    }

    &__item {
      display: inline-block;

      &:last-child .nav__link {
        margin-right: 0;
      }
    }

    &__link {
      color: $color_nav_link;
      padding-right: 0;
      padding-left: 0;
      margin-right: 17px;
      margin-left: 17px;
      overflow: hidden;

      &,
      &--active {
        &::after {
          content: "";
          background-color: #878787;
          width: 100%;
          height: 2px;
          position: absolute;
          bottom: 0;
          left: 0;
          transition: all .3s;
        }
      }

      &::after {
        transform: translateX(-110%);
      }

      &:hover::after,
      &--active::after {
        transform: translateX(0);
      }
    }
  }

  /* Home section */
  .home {
    &__inner {
      align-items: flex-start;
    }

    &__img {
      width: 49%;
      max-width: 570px;
      margin: 5px 0 7px 0;
    }

    &__desc {
      text-align: left;
      margin-top: 161px;
      margin-left: 0;
    }

    &__title {
      font-size: 90px;
      line-height: 1;
      margin-left: -4px;
      margin-bottom: 1px;
    }

    &__text {
      font-size: 21px;
    }

    &__btns {
      margin-left: 3px;
    }

    &__btn {
      margin-right: 0;
      margin-left: 0;

      &.about__btn {
        margin-right: 15px;
      }
    }
  }

  /* About section */
  .about {
    padding-top: 59px;
    margin-bottom: 104px;

    &__inner {
      align-items: flex-start;
    }

    &__desc {
      padding: 69px 75px 0 3px;
    }

    &__text {
      margin-top: -2px;
      margin-bottom: 32px;
    }

    .download__btn {
      min-width: 246px;
    }
  }

  .section-heading {
    font-size: 46px;
    letter-spacing: 5px;
    padding-top: 17px;
  }

  /* Services section */
  .services {
    padding-top: 119px;
    padding-bottom: 40px;
    margin-bottom: 80px;

    &__list {
      justify-content: space-between;
    }

    &__heading {
      margin-bottom: 47px;
    }

    &__item {
      max-width: 369px;
      margin-bottom: 70px;
    }
  }

  .service-block {
    &__heading {
      font-size: 24px;
    }
  }

  /* Skills section */
  .skills {
    padding-top: 37px;
    padding-bottom: 125px;

    &__heading {
      margin-bottom: 44px;
    }
  }

  /* Portfolio section */
  .portfolio {
    padding-top: 126px;
    padding-bottom: 123px;

    &__heading {
      margin-bottom: 36px;
    }

    &__item {
      max-width: 369px;
    }

    &__btns {
      margin-top: -2px;
    }

    &__btn {
      min-width: 190px;
    }
  }

  /* Reviews section */
  .reviews {
    padding-top: 126px;
    padding-bottom: 123px;

    &__heading {
      margin-bottom: 49px;
    }
  }

  .reviews-slider {
    &-container {
      padding-top: 81px;
    }

    &__photo {
      margin-left: 122px;
    }

    &__username {
      font-size: 30px;
      line-height: 1.1;
      margin-bottom: 13px;
    }
  }

  /* Progress section */
  .progress {
    padding-top: 52px;
    padding-bottom: 29px;
    margin-bottom: 154px;

    &__list {
      justify-content: space-between;
    }

    &__item {
      max-width: 268px;
    }

    &__ico {
      margin-bottom: 19px;
    }

    &__desc {
      font-size: 22px;
    }

    &__counter {
      font-size: 30px;
      line-height: 1.3;
    }
  }

  /* Footer */
  .footer {
    padding-top: 145px;

    &-bottom {
      @include flex_row_wrap;
      justify-content: flex-start;
    }

    &-social {
      margin-bottom: 0;

      &__link {
        &:hover {
          background-color: #0084b4;
        }

        &:hover .footer-social__link_ico path {
          fill: #fff;
        }
      }
    }

    &-copyright-container {
      padding-top: 4px;
      width: 60.3%;
    }
  }

  /* Contact form */
  .form-container {
    &__heading {
      margin-bottom: 61px;
    }
  }

  .contact-form {
    padding-bottom: 58px;
  }

  .scroll-top {
    right: 58px;
    bottom: 193px;
  }

  /* Hover elements */
  .logo {
    transition: opacity .3s;

    &:hover {
      opacity: .9;
    }
  }

  .page-btn {
    position: relative;
    overflow: hidden;

    &__text {
      position: relative;
      z-index: 1;
    }

    &::after {
      content: "";
      width: 0;
      height: 100%;
      background-color: #f6f6f6;
      position: absolute;
      top: 0;
      left: 50%;
      right: 50%;
      transition: all .3s;
    }

    &:hover::after {
      width: 100%;
      left: 0;
      right: 0;
      transform: scale(1.4);
    }
  }
}

@media screen and (min-width: 1117px) {
  /* Portfolio section */
  .portfolio {
    &__content {
      justify-content: space-between;
    }
  }
}
